@import "./_variables";
@import "./_mixins";

.banner {
    position: relative;
    height: 800px;
    font-family: 'Roboto';
    background-image: url(../images/banner/banner.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-600px);
        width: calc(100% - 360px);
        height: 690px;
        border-radius: 0 0 0 150px;
        background-image: linear-gradient(to right, #ffd164, #41c274, rgba(0, 88, 132, 0.5) 85%, rgba(0, 67, 176, 0) 100%);
        opacity: 0.8;
    }

    &-wrap {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        box-sizing: border-box;
        width: 100%;
        max-width: 1200px;
        margin: auto;
        padding: 0 20px;
    }

    &-box {
        max-width: 630px;
        padding-top: 160px;
        padding-left: 65px;
    }

    &-title {
        font-size: 70px;
        font-weight: 700;
        line-height: 1.1;
        letter-spacing: 2px;
        color: #fff;
        text-shadow: 0 0 13px rgba(0, 0, 0, 0.8);
    }

    &-text {
        margin: 50px 0;
        font-size: 26px;
        color: #fff;
        text-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
    }

    &-btn {
        box-sizing: border-box;
        display: inline-block;
        padding: 15px 40px;
        font-size: 27px;
        font-weight: 500;
        color: #129abb;
        text-decoration: none;
        border-radius: 36px;
        background-color: #fff;
        transition: .3s;

        &:hover {
            color: #fff;
            background-color: #129abb;
        }
    }

    @include breakpoint-down('desktop') {
        height: 500px;

        &::before {
            width: calc(100% - 100px);
            height: 450px;
            border-radius: 0 0 0 120px;
        }

        &-wrap {
            padding: 0 40px;
        }

        &-box {
            padding-top: 80px;
        }

        &-title {
            font-size: 50px;
        }

        &-text {
            margin: 35px 0;
            font-size: 20px;
        }

        &-btn {
            padding: 12px 30px;
            font-size: 21px;
        }
    }

    @include breakpoint-down('laptop') {
        &::before {
            left: 40px;
            transform: none;
            width: calc(100% - 40px);
        }
    }

    @include breakpoint-down('tablet') {
        &::before {
            left: 20px;
            width: calc(100% - 20px);
            border-radius: 0 0 0 80px;
        }

        &-wrap {
            padding: 0 20px;
        }

        &-box {
            padding-top: 70px;
            padding-left: 35px
        }

        &-title {
            font-size: 42px;
        }

        &-text {
            margin: 30px 0;
            font-size: 18px;
        }

        &-btn {
            padding: 10px 25px;
            font-size: 19px;
        }
    }
}

.title {
    margin-bottom: 50px;

    @include breakpoint-down('desktop') {
        margin-bottom: 36px;
    }

    @include breakpoint-down('tablet') {
        margin-bottom: 25px;
    }
}

.intro {
    padding: 100px 0;
    text-align: center;

    &-wrap {
        box-sizing: border-box;
    }

    &-text {
        font-size: 18px;
        line-height: 2;
        color: #8b8b8b;
        letter-spacing: 1px;
    }

    &-content {
        display: flex;
        align-items: center;
        margin-top: 85px;
    }

    &-pic {
        flex: 1;
        max-width: 920px;
        margin-right: 40px;

        img {
            width: 100%;
        }
    }

    &-ball {
        flex: 0 0 auto;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        width: 680px;
    }

    &-item {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 200px;
        height: 200px;
        margin: 30px 70px;
        padding: 30px 20px 0;
        font-size: 18px;
        color: #8b8b8b;
        line-height: 1.5;
        border-radius: 50%;

        p {
            margin-bottom: 12px;
            font-size: 24px;
            font-weight: 500;
            line-height: 1.2;
            letter-spacing: 2.5px;
            color: #000;
        }

        &.yl {
            background-color: #ffedc1;
            box-shadow: 0 0 45px 45px #ffedc1;
        }

        &.bu {
            background-color: #9bd5e1;
            box-shadow: 0 0 45px 45px #9bd5e1;
        }

        &.gn {
            background-color: #9be5c3;
            box-shadow: 0 0 45px 45px #9be5c3;
        }
    }

    &-break {
        flex: 0 0 100%;
        height: 0;
    }

    @include breakpoint-down('desktop') {
        &-text {
            font-size: 16px;
        }

        &-content {
            margin-top: 60px;
        }

        &-ball {
            box-sizing: border-box;
            width: 560px;
            padding-right: 20px;
        }

        &-item {
            width: 150px;
            height: 150px;
            margin: 45px 60px;
            padding: 8px 0 0;
            font-size: 16px;

            p {
                font-size: 22px;
            }

            &.yl {
                box-shadow: 0 0 40px 40px #ffedc1;
            }

            &.bu {
                box-shadow: 0 0 40px 40px #9bd5e1;
            }

            &.gn {
                box-shadow: 0 0 40px 40px #9be5c3;
            }
        }
    }

    @include breakpoint-down('laptop') {
        padding: 70px 0;

        &-pic {
            flex: 0 0 auto;
            width: 50%;
            margin-right: 10px;
        }

        &-ball {
            width: 508px;
            padding-right: 0;
        }

        &-item {
            margin: 40px 52px;
        }
    }

    @include breakpoint-down('tablet') {
        padding: 60px 0;

        &-wrap {
            padding: 0 20px;
        }

        &-text {
            font-size: 15px;
            line-height: 1.7;
            letter-spacing: 0;
        }

        &-content {
            display: block;
            margin-top: 50px;
        }

        &-pic {
            width: calc(100% - 300px);
            margin-right: 0;
        }

        &-ball {
            width: 360px;
            margin-top: -230px;
            margin-left: auto;
        }

        &-item {
            width: 120px;
            height: 120px;
            margin: 20px 30px;
            font-size: 14px;

            p {
                margin-bottom: 6px;
                font-size: 17px;
            }

            &.yl {
                box-shadow: 0 0 25px 25px #ffedc1;
            }

            &.bu {
                box-shadow: 0 0 25px 25px #9bd5e1;
            }

            &.gn {
                box-shadow: 0 0 25px 25px #9be5c3;
            }
        }
    }

    @include breakpoint-down('mobile') {
        &-content {
            margin-top: 40px;
        }

        &-pic {
            width: 100%;
        }

        &-ball {
            width: calc(100% + 40px);
            margin: 30px -20px 0;
        }
    }
}

.total {
    position: relative;
    padding: 100px 0;
    background-color: #fffcf7;
    background-image: url(../images/bg/dna.png);
    background-repeat: no-repeat;
    background-position: right bottom;

    &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        display: block;
        width: 100%;
        height: 3px;
        background-image: linear-gradient(to right, #F8D074, #56B770, #1A8FAD);
    }

    &-wrap {
        box-sizing: border-box;
        max-width: 1240px;
        margin: 0 auto;
        padding: 0 20px;
    }

    &-content {
        display: flex;
        align-items: center;
    }

    &-wording {
        flex: 1;
        padding-top: 60px;
        margin-right: 100px;
    }

    &-pic {
        position: relative;
        flex: 0 0 auto;
        width: 51%;
        border-radius: 0 0 0 80px;
        box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.15);
        overflow: hidden;
        z-index: 1;

        img {
            width: 100%;
        }
    }

    &-title {
        display: inline-block;
        margin-bottom: 20px;
        margin-left: 30px;
        font-size: 48px;
        font-weight: bold;
        letter-spacing: 5px;
        background-image: linear-gradient(to right, #47c164, #0095ae);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    &-text {
        margin-bottom: 35px;
        margin-left: 30px;
        font-size: 24px;
        font-weight: 500;
        letter-spacing: 2.5px;
    }

    &-point {
        position: relative;
        display: inline-block;
        padding: 10px 40px;
        font-size: 24px;
        font-weight: 500;
        letter-spacing: 2.5px;
        color: #fff;
        text-align: center;
        border-radius: 28px;
        background-color: #00937f;

        &::after {
            content: '';
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateX(100%);
            width: 200px;
            height: 3px;
            background-color: #00937f;
        }
    }

    &-number {
        display: flex;
        justify-content: center;
        margin-top: 110px;
    }

    &-item {
        flex: 0 0 auto;
        width: 33.3333%;
        font-size: 24px;
        font-weight: bold;
        text-align: center;

        p {
            margin-top: 20px;
            font-size: 100px;
            font-family: 'Bebas Neue';
            color: #00937f;
            transform: scaleX(1.1);
        }
    }

    &-slogan {
        margin-top: 70px;
        font-size: 24px;
        font-weight: 500;
        letter-spacing: 2.5px;
        text-align: center;
        color: #8b8b8b;
    }

    @include breakpoint-down('desktop') {
        &-title {
            font-size: 40px;
        }

        &-text {
            font-size: 21px;
        }

        &-point {
            padding: 10px 35px;
            font-size: 21px;
        }

        &-number {
            margin-top: 80px;
        }

        &-item {
            font-size: 22px;

            p {
                margin-top: 10px;
                font-size: 96px;
            }
        }

        &-slogan {
            margin-top: 60px;
            font-size: 20px;
        }
    }

    @include breakpoint-down('laptop') {
        padding: 70px 0;

        &-wording {
            padding-top: 40px;
            margin-right: 60px;
        }

        &-title {
            margin-left: 25px;
            font-size: 38px;
        }

        &-text {
            margin-left: 25px;
            font-size: 19px;
        }

        &-point {
            font-size: 19px;
        }

        &-number {
            margin-top: 65px;
        }

        &-item {
            font-size: 20px;

            p {
                margin-top: 5px;
                font-size: 90px;
            }
        }

        &-slogan {
            margin-top: 30px;
            font-size: 18px;
        }
    }

    @include breakpoint-down('tablet') {
        padding: 60px 0;
        background-size: contain;

        &-pic {
            width: 49%;
            border-radius: 0 0 0 50px;
        }

        &-wording {
            padding-top: 10px;
            margin-right: 30px;
        }

        &-title {
            margin-bottom: 15px;
            margin-left: 20px;
            font-size: 32px;
            letter-spacing: 4px;
        }

        &-text {
            margin-bottom: 25px;
            margin-left: 20px;
            font-size: 17px;
            letter-spacing: 1.5px;
        }

        &-point {
            padding: 7px 29px;
            font-size: 17px;
            letter-spacing: 1.5px;
        }

        &-number {
            margin-top: 45px;
        }

        &-item {
            font-size: 18px;

            p {
                margin-top: 0;
                font-size: 72px;
            }
        }

        &-slogan {
            margin-top: 20px;
            font-size: 16px;
        }
    }

    @include breakpoint-down('mobile') {
        text-align: center;

        &-content {
            display: block;
        }

        &-pic {
            width: 100%;
        }

        &-wording {
            padding-top: 0;
            margin-right: 0;
            margin-bottom: 50px;
        }

        &-title {
            margin-left: 0;
        }

        &-text {
            margin-left: 0;
        }

        &-point {
            padding: 8px 18px;

            &::after {
                right: 50%;
                top: 100%;
                transform: translateX(50%);
                width: 3px;
                height: 50px;
            }
        }

        &-number {
            margin-top: 35px;
            flex-wrap: wrap;
        }

        &-item {
            width: 50%;

            p {
                font-size: 56px;
            }
        }

        &-slogan {
            margin-top: 35px;
            font-size: 15px;
        }
    }
}

.step {
    padding: 120px 0;
    background-image: url(../images/bg/light/index.png);
    background-repeat: no-repeat;
    background-position: top 57px right;

    &-wrap {
        box-sizing: border-box;
        max-width: 1520px;
        margin: 0 auto;
    }

    &-content {
        display: flex;
        padding-top: 20px;
    }

    &-item {
        flex: 0 0 auto;
        box-sizing: border-box;
        width: 25%;
        padding: 0 50px;
        counter-increment: item;
    }

    &-item+&-item &-pic::after {
        content: '';
        position: absolute;
        top: 50%;
        left: -50px;
        transform: translate(-50%, -50%);
        width: 16px;
        height: 38px;
        background-image: url(../images/icon/arrow.svg);
        background-repeat: no-repeat;
        background-position: center left;
        background-size: contain;
    }

    &-pic {
        position: relative;

        &::before {
            content: counter(item, decimal);
            position: absolute;
            top: -13px;
            left: -13px;
            box-sizing: border-box;
            width: 50px;
            height: 50px;
            padding: 4.5px;
            font-size: 35px;
            line-height: 1;
            color: #fff;
            text-align: center;
            border-radius: 0 0 20px 0;
            background-image: linear-gradient(135deg, #fdce63, #3abd67, #0094ac);
        }

        &>div {
            border-radius: 0 0 50px 0;
            overflow: hidden;
        }

        img {
            width: 100%;
        }
    }

    &-title {
        margin: 16px 0;
        font-size: 32px;
        font-weight: 500;
    }

    &-list {
        font-size: 21px;
        color: #8b8b8b;

        ul {
            margin: 0;
            padding-left: 30px;
            line-height: 1.2;

            li+li {
                margin-top: 12px;
            }
        }
    }

    @include breakpoint-down('desktop') {
        &-item {
            padding: 0 40px;
        }

        &-item+&-item &-pic::after {
            left: -40px;
        }

        &-pic {
            &::before {
                width: 42px;
                height: 42px;
                font-size: 28px;
                border-radius: 0 0 15px 0;
            }
        }

        &-title {
            font-size: 28px;
        }

        &-list {
            font-size: 18px;
        }
    }

    @include breakpoint-down('laptop') {
        padding: 80px 0;

        &-item {
            padding: 0 30px;
        }

        &-item+&-item &-pic::after {
            left: -30px;
            width: 13px;
            height: 31px;
        }

        &-pic {
            &>div {
                border-radius: 0 0 40px 0;
            }
        }

        &-title {
            margin: 10px 0;
            font-size: 24px;
        }

        &-list {
            font-size: 16px;


            ul {
                padding-left: 28px;

                li+li {
                    margin-top: 8px;
                }
            }
        }
    }

    @include breakpoint-down('tablet') {
        padding: 60px 0;

        &-content {
            flex-wrap: wrap;
            margin: -30px 0;
            padding: 20px 30px 0;
        }

        &-item {
            width: 50%;
            margin: 30px 0;
        }
    }

    @include breakpoint-down('mobile') {
        &-content {
            display: block;
            margin: 0;
            padding: 20px 20px 0;
        }

        &-item {
            width: 100%;
            margin: 0;

            &+& {
                margin-top: 60px;
            }
        }

        &-item+&-item &-pic::after {
            top: -30px;
            left: 50%;
            transform: translate(-50%, -50%) rotate(90deg);
        }

        &-pic {
            &::before {
                top: -8px;
                left: -8px;
                width: 35px;
                height: 35px;
                padding: 4px;
                font-size: 22px;
            }

            &>div {
                border-radius: 0 0 30px 0;
            }
        }

        &-title {
            font-size: 20px;
        }

        &-list {
            font-size: 14px;

            ul {
                padding-left: 18px;

                li+li {
                    margin-top: 5px;
                }
            }
        }
    }
}

.tranBanner {
    position: relative;
    height: 490px;
    overflow: hidden;
    background-image: url(../images/banner/transition.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;

    &-wrap {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
    }

    img {
        width: 1920px;
        // transition: .3s;
        // transition-timing-function: ease-out;
    }

    @include breakpoint-down('desktop') {
        height: 360px;

        img {
            width: 1440px;
        }
    }

    @include breakpoint-down('tablet') {
        height: 260px;

        img {
            width: 1024px;
        }
    }

    @include breakpoint-down('mobile') {
        // height: 160px;
        background-attachment: unset;

        img {
            width: 768px;
        }
    }
}

.article {
    padding: 100px 0;
    background-image: url(../images/bg/laboratory.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    &-wrap {
        box-sizing: border-box;
        max-width: 1240px;
        margin: 0 auto;
        padding: 0 20px;
    }

    &-content {
        padding-top: 20px;
    }

    @include breakpoint-down('mobile') {
        &-wrap {
            padding: 0 20px;
        }
    }
}

@import "./_utilities";